/*left column*/
.has-left-column {
	padding: 0;
}
.sb-left-column {
	.sb-widget {
		margin-bottom: 35px;
		padding: 0 25px;
		.js-slider-controls {
			a {				
				margin-top: 21px;
				width: 25px;
				height: 25px;
				svg {
					width: 14px;
					height: 14px;
				}
			}
			.js-next-button {
				@include ab(n,n,n,20px);
			}
		}
	}
	.sb-widget_title {
		text-transform: uppercase;
		padding: 25px 0 20px;
		border-bottom: 1px solid var(--body-border-color);
		h5 {
			font-weight: 500;
		}
	}
	.widget-menu{
		border: 2px solid var(--main-color);
		padding: 0;
		.sb-widget_title {
			background: var(--main-color);
			padding: 16px 25px;
		}
		svg {
			transition: 0.2s all ease-out;
			transform: rotate(-90deg);
		}
		>.menu-list {
			padding: 15px 25px;
			>li:not(:last-child) {
				border-bottom: 1px solid var(--body-border-color);
			}
		}
		.js-btn-collapse {
			width: 40px;
			height: 20px;
			display: flex;
			align-items: center;
			@include flex-end;
			&.is-open {
				svg {
					transform: rotate(0);
				}
			}
		}
		.menu-item {
			padding: 10px 0;
			.dropdown-menu {
				width: 100%;
				height: 0;
				overflow: hidden;
				.menu-list {
					overflow: hidden;
					visibility: hidden;
					opacity: 0;
					transition: 0.2s all ease-out;
					margin: 6px 0 0 18px;
					&.is-open {
						display: block;
						visibility: visible;
						opacity: 1;
					}
					a {						
						&:before {
							content: "-";
							display: inline-block;
							width: 15px;
						}
					}
					.js-btn-collapse {
						&:before {
							display: none;
						}
					}
				}
			}
		}
	}
	.widget-iconbox {
		.card_section-item-inner {
			display: flex;
			align-items: center;      
			height: 100%;
			.card_section-header {            
				.image__style {
					min-width: 40px;
					width: 100%;
					height: 40px;
				}
			}
		}
		.card_section-info {
			p {
				margin: 0;    
				font-size: 12px; 
				line-height: 1.68;                        
			}
			.card_section-title {
				font-weight: 600; 
				font-size: var(--body-font-size);
				margin: 0;
				text-transform: uppercase;
				line-height: 1.5em;
			}
		}
		.card_section-item {
			padding: 20px 0;
			&:not(:last-child) {
				border-bottom: 1px solid var(--body-border-color);
			}
			.card_section-figure {
				@include margin-end(15px);
				@extend %transition; 
				svg {
					width: 38px;
					height: 38px;
					fill: var(--main-color);
					path {
						fill: var(--main-color);
					}
				}
				&:hover {
					@media (hover:hover) {
						transform: scale(1.1);
					}
				}
			}
		}
	}
	.widget-logo-list {
		.logo-list {
			display: grid;
			grid-gap: 15px;
			grid-template-columns: repeat(2, 1fr);
			padding: 25px 0 35px;
			.logo-item {
				padding: 0;
				margin-bottom: 10px;
				img {
					transform: scale(1);
					@extend %transition;
				}
				&:hover {
					@media (hover:hover) {
						img {
							transform: scale(1.1);
						}
					}
				} 
			}
		}
	}
	.widget-banner {
		padding: 0;
		position: relative;
		.banner {
			min-height: 300px;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.textbox {
				text-align: center;
				padding: 30px 10px;
				>div {
					width: 100%;
				}
				.btn {
					padding: 0 20px;
					height: 40px;
					line-height: 40px;
				}
				.text-4 {
					.d-flex {
						align-items: center;
						justify-content: center;
						line-height: 1.1;
					}
					span {
						display: block;
						line-height: 1;
						font-size: var(--body-14);
						&:first-child {
							font-size: 30px;
						}
					}
				}
			}
		}
	}
	.widget-product-list {
		position: relative;
		.product-list_wrapper {
			padding-bottom: 15px;
		}
		.label_ui-group {
			left: 0;
		}
		.product-item {
			.product-card {
				padding: 0;
				box-shadow: none;
				margin-top: 30px;
				margin-bottom: 15px;
				.featured-product_wrapper {
					padding: 0 4px;
				}
			}
			.price-compare {
				display: none;
			}
		}
	}
	.widget-testimonial-list {
		position: relative;
		.testimonial-list {
			padding-bottom: 15px;
			.col-12 {
				padding: 0;
			}
		}
		.card_testimonial-author {
			text-transform: uppercase;
			font-weight: 500;
			margin: 0 0 var(--gutter-xxs);
		}
		.card_testimonial-review {
			font-weight: 400;
			line-height: 1.618;
			p {
				margin: 0 0 var(--gutter-xs);
			}
		}
		.card_testimonial-image {
			img {
				@extend %radius-circle;
			}
		}
		.card_section-item-inner {
			margin: 20px 0;
		}
		.card_testimonial-image {
			display: flex;
			align-items: center;
			margin-bottom: var(--gutter-xs);
			.image {
				width: 60px;
				height: 60px;
			}
			.content {
				@include padding-start(var(--gutter-xs));
			}
		}
		.card_testimonial-review {
			font-size: var(--body-font-size);
			p {
				margin: 0;
			}
		}
	}
}
/*end left column*/