.home-slideshow, .logo-list, .slideshow-v2, .home-slideshow-banner {	
	.js-slider-controls {
		a {
			width: 50px;
			height: 50px;
			margin-top: -25px;
			@extend %radius-circle;
			background: #fff;
			top: 50%;
			box-shadow: inset 0 0 3px rgba(0,0,0,0.7);
			svg {
				width: 14px;
				height: 14px;
			}
		}
		.js-prev-button {
			@include ab(n,-10px,n,n);
			&:after {
				display: none;
			}
		}
		.js-next-button {
			@include ab(n,n,n,-10px);
		}
	}
}
.home-slideshow {
	.section-content {
		position: relative;
		.slide-content-inner {
			padding: 0 var(--gutter-xs);
			width: auto;
		}
	}
	.slide-content-inner >div {
		animation-fill-mode: backwards;
		animation-iteration-count: 1;
	}
	.section-content-wrapper {
		.overlay-ui:not(img) {
			display: table;
			width: fit-content;
		}
	}
	.slideshow-wrapper {
		.col-12 {
			padding: 0;
		}
		.slide-item {
			position: relative;
		}
		.content-product_label {
			.label_ui {
				display: inline-block;
			}
		}
		.content-text-3 {
			letter-spacing: 0.5px;
		}
		.content-product .product-price {
			margin: 0;
		}
		.slide-content-wrapper {
			@include ab(0,0,0,0);
			max-width: 1200px;
			margin: 0 auto;
			width: 100%;
			padding: 0 var(--gutter-xs);
		}
		.content-sale-label {
			width: 66px;
			height: 66px;
			line-height: 1.2;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;        
			font-weight: 700;
			font-size: 4.5rem;
			background-color: var(--btn1-bg-cl);
			color: var(--btn1-cl);
			@extend %radius-circle;
			&.overlay-ui {                        
				@include translate(-54%, -20%);
				@include ab(20%, 54%, n, n);
			}
			.sale-label {
				display: flex;
				margin-bottom: 3px;
			}
		}  
	}
	.js-slider-controls {
		.js-prev-button {
			@include ab(n,3%,n,n);
		}
		.js-next-button {
			@include ab(n,n,n,3%);
		}
	}
}

/*slideshow-v2*/
.slideshow-v2 {
	position: relative;
	.section-wrapper {
		position: relative;
		.tns-outer {
			overflow: hidden;
		}
	}
	.slideshow-image {
		.image {
			margin: 0 auto;
		}
	}
	.slideshow-text {
		padding: 20px 0;
		text-align: center;
		.text-1 {
			margin-bottom: var(--gutter-sm);
			letter-spacing: 1.5px;
		}
		.text-2 {
			margin-bottom: var(--gutter-sm);
			letter-spacing: 1.5px;
			line-height: 1.5;
		}
		.slideshow-button {
			margin-top: var(--gutter-lg);
		}
		.countdown {
			margin-bottom: var(--gutter-xlg);
			.countdown_item {
				padding: 0 13px;
				position: relative;
				span {
					display: block;
				}
				.number {
					color: var(--main-color);
					min-width: 45px;
				}
				.label {
					font-weight: var(--body-font-weight);
					text-transform: uppercase;
					font-size: 10px;
					opacity: 0.6;
				}
			}
			.seconds, .minutes, .hours {
				&:before {
					content: ":";
					@include ab(5px,-3px,n,n);
					color: var(--miscellaneous-color);
					height: 100%;
					font-size: 26px;
					font-weight: 100;
				}
			}
		}
	}
	.slideshow-item {
		.row {
			align-items: center;
		}
	}
	.tns-nav {
		margin-bottom: 20px;
	}
}
/*end slideshow-v2*/

/*slideshow menu*/
.home-slideshow-menu {
	.section-content {
		.col-lg-3 {
			margin-bottom: 15px;
			@media (min-width: 992px) {
				margin-bottom: 0;
			}
		}
	}
	.section-content__menu {
		border: 2px solid var(--main-color);
		padding: 15px;
		@media (min-width: 1200px) {
			padding: 15px 25px;
		}
		.menu-list {
			>li:not(:last-child) {
				border-bottom: 1px solid var(--body-border-color);
			}
			.label {
				font-size: 11px;
				min-width: 35px;
				line-height: 17px;
				border-radius: 3px;
				text-align: center;
				height: 17px;
				text-transform: capitalize;
				display: inline-block;
				position: relative;
				top: -1px;
			}
			>li {
				position: relative;
			}
			li {
				padding: 10px 4px;
				font-size: var(--body-14);
				.js-btn-collapse {
					width: 30px;
					height: 24px;
					display: flex;
					align-items: center;
					justify-content: flex-end;
					@include ab(8px,n,n,0);
					svg {
						width: 10px;
						height: 10px;
						transition: 0.2s all ease-out;
					}
					&.is-open {
						svg {
							transform: rotate(90deg);
						}
					}
				}
			}
			.dropdown-menu {
				overflow: hidden;
				transition: 0.2s all ease-out;
				width: 100%;
				height: 0;
				opacity: 0;
				visibility: hidden;
				>ul {
					margin: 5px 0 0 10px;
					@media (min-width: 1200px) {
						margin-left: 18px;
					}
				}
				li {
					border: 0;
					a {
						&:before {
							content: "-";
							display: inline-block;
							width: 13px;
						}
					}
				}
				&.is-open {
					visibility: visible;
					opacity: 1;
					height: 100%;
				}
			}
		}
	}
}
/*end slideshow-menu*/

/*slideshow banner*/
.home-slideshow-banner {
	.slideshow-content {
		position: relative;
		.col-12 {
			padding: 0;
		}
	}
	@media (min-width: 768px) {
		.banner-wrapper {
			grid-template-columns: repeat(2,1fr);
		}
	}
	@media (min-width: 992px) {
		.banner-wrapper {
			grid-template-columns: auto;
		}
	}
}
/*end slideshow banner*/