.custom-content {
	.custom-content__item {
		height: 100%;
		.custom-content__product {
			height: 100%;
		}
		.banner__item {
			position: relative;
			overflow: hidden;
			.textbox {
				padding: 30px;
				display: table;
				width: fit-content;
				.subheading {
					margin-bottom: 15px;
				}
				.caption {
					margin-bottom: 20px;
				}
				.btn {
					height: 36px;
					line-height: 20px;
					font-size: 12px;
				}
			}
			.image {
				transition: opacity .35s,transform .35s;
				transform: scale(1);
			}
			@media (min-width: 768px) {
				.textbox {
					padding: 15px 20px;
				}
			}
			@media (min-width: 1200px) {
				.textbox {
					padding: 30px;
					.btn {
						min-height: 40px;
						line-height: 24px;
						font-size: var(--body-14);
					}
				}
			}
			@media (min-width: 1025px) {	
				.btn {
					transform: translateY(10px);
				}			
				&:hover {
					.image {						
						transform: scale(1.02);
					}
					.btn {
						transform: translateY(0px);
					}
				}
			}
		}
		@media (min-width: 768px) {
			.banner {
				.banner__item:last-child {
					margin-bottom: 0!important;
				}
			}
		}
	}
}
