.home-categories {
	.section_content-wrapper {		
		position: relative;
	}
	.slider-list {
		display: flex;
	}
	&.has-border {
		.section_title-wrapper {
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			justify-content: space-between;
			border-bottom: 1px solid var(--body-border-color);
			@media (min-width: 768px) {
				padding-bottom: 12px;
			}
		}
	}
	.card_section-item-inner {
		height: 100%;
		display: flex;
		box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.0);
		transition: box-shadow 300ms ease-out;
		&:hover {
			@media (hover:hover) {
				box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.15);
			}
		}
		.card_section-body {
			padding-top: 12px;
		}
		.card_section-info, .title {
			font-size: 15px;
			font-weight: 500;
			display: block;
			margin-bottom: 8px;
			text-transform: uppercase;
			text-align: left;
			p {
				margin: 0;
			}
		}
		.card_section-menu {
			li {
				padding: 5px 0;
				a {
					@include clamp(1);
					&:hover {
						color: var(--main-color);
					}
				}
			}
		}
		.btn-show-all {
			color: var(--main-color);
			text-transform: capitalize;
			&:hover {
				color: var(--body-link);
			}
			>div {
				display: flex;
				align-items: center;
				margin-top: 8px;
				padding: 5px 0;
				.svg_emlement-ui {
					@include margin-start(4px);
				}
				svg {
					width: 11px;
					height: 11px;
					fill: var(--main-color);
				}
			}
			&:hover {
				>div svg {
					fill: var(--body-link);
				}
			}
		}
	}
	.section_content-body .js-tns {
		display: flex;
		align-items: stretch;
		.card_section-item {
			margin-top: 4px;
			margin-bottom: 6px;
		}
	}
	.card_section-menu {
		height: 100%;
		display: flex;
		justify-content: space-between;
		flex-direction: column;
	}
	&.style-1 {
		.card_section-item-inner {
			padding: 30px;
			flex-direction: column;
			.card_section-body {
				flex: 1;
			}
		}
	}
	&.style-2 {
		.card_section-item-inner {
			padding: 20px 10px;
			>div {
				flex: 1;
			}
			.card_section-header {
				max-width: 150px;
				width: 100%;
			}
		}
		.card_section-body {
			@include margin-start(15px);
		}
	}
	.section_content-body .no-menu {
		.menu-item.title {
			padding: 0;
			margin: 0;
		}
	}
}
