.home-gallery {
	.section_content-wrapper {
		.card_section-item-inner{
			position: relative;
			overflow: hidden;
		}
	}
	.card_section-content {
		padding: var(--gutter-sm);
		z-index: layer(card_section-content);
	}
	.card_section-figure {
		min-height: 100px;
		img {
			min-height: 100px;
		}
	}
	.style-2 {
		.card_section-figure {
			min-height: 150px;
			.image__style {
				height: 150px;
			}
			img {
				object-fit: cover;
			}
		}
		.right-content {
			display: none;
			@media (min-width: 992px) {
				display: block;	
			}
			.card_section-text-4 {
				.d-flex {
					align-items: center;
					justify-content: center;
					line-height: 1.1;
					span {
						display: block;
						font-size: var(--body-14);
						line-height: 1;
						margin-left: 10px;
						&:first-child {
							font-size: 45px;
						}
					}
				}
			}
		}
	}
	@media (min-width: 1025px) {		
		&.hover_style-1 {
			.card_section-item-inner {
				.card_section-figure {
					@include effect-img;
				}
				&:hover {
					.card_section-figure {					
						@include hover-effect-img;
					}
				}
			}
		}
		&.hover_style-2 {
			.card_section-figure {
				width: calc(100% + 20px);
				max-width: calc(100% + 20px);
				transition: opacity .35s,transform .35s;
				transform: translate3d(-10px,0,0);
				backface-visibility: hidden;
				&:before {
					content: "";
					@include ab(0,0,n,0);
					height: 100%;
					width: 100%;
					opacity: 0;
					visibility: hidden;
					transition: transform 0.35s ease,opacity 0.35s ease;
					background: var(--main-color);
					z-index: layer(hover_style-2);
				}
			}
			.style-1 {
				.card_section-content {
					.card_section-button {
						position: relative;
						&:before {
							content: "";
							@include ab(-5px,0,n,n);
							width: 100%;
							transition: transform 0.35s ease,opacity 0.35s ease;
							height: 2px;
							opacity: 0;
							visibility: hidden;
							transform: translateX(-100%);
							background: var(--miscellaneous-color);
						}
						a {
							position: relative;
							top: 0;
							transition: 0.3s all ease-in-out;
						}
					}
				}
			}
			.card_section-item-inner {
				&:hover {
					.card_section-figure {
						transform: translate3d(0,0,0);
						&:before {							
							width: 100%;
							opacity: 0.45;
							visibility: visible;
						}
					}
				}
			}
			.style-1 {
				.card_section-item-inner {
					&:hover {						
						.card_section-content {
							.card_section-button {
								&:before {
									opacity: 1;
									visibility: visible;
									transform: translateX(0);
								}
								a {
									top: 12px;
								}
							}
						}
					}
				}
			}
		}
	}
}

/*multilple banner*/
.multiple-banner {
	position: relative;
	.section-content {
		>div:nth-child(odd) {
			.banner-item_content {
				@include margin-start(auto);
			}
		}
	}
	.banner-item_wrapper {
		.banner-item_image {
			min-height: 190px; 
		}
		.image {
			img {
				min-height: 190px;
				object-fit: cover;
			}
		}
		.banner-item_content {
			max-width: 586px;		
			padding: 15px 20px;		
			@include ab(n,0,0,0);
			display: flex;
			flex-wrap: wrap;
			align-content: center;
			>div {
				width: 100%;
				display: block;
				p {
					margin: 2px 0;
				}
				a {
					color: inherit;
				}
			}
			.btn-banner {
				display: inline-flex;
				align-items: center;
				height: 44px;
				border-radius: 5px;
				padding: 5px 15px;
				svg {
					width: 24px;
					height: 24px;
					@include margin-end(10px);
				}
				.custom-btn {
					span {						
						display: block;
						line-height: 1.4;
						&:first-child {
							font-size: 9px;
							line-height: 1;
						}
					}
				}
				&:hover {
					opacity: 0.85;
				}
			}
			>a {
				&:last-child {
					@include margin-start(10px);
				}
			}
		}
	}
	@media (min-width: 1200px) {
		.banner-item_wrapper .banner-item_content {
			padding-left: 50px;
			padding-right: 50px;
		}
	}
	@media (min-width: 1440px) {
		.banner-item_wrapper .banner-item_content {
			.btn-banner {
				font-size: 16px;
			}
			>a {
				&:last-child {
					@include margin-start(20px);
				}
			}
		}
	}
}
/*end multiple banner*/

/*single banner*/
.single-banner {
	.banner-content {
		padding: 30px 20px;
		p {
			margin: 0;
		}
	}
	.right-content {
		.text-4 {
			position: relative;
			@include margin-end(10px);
			display: flex;
			align-items: center;
		}
		.svg-wrapper {
			width: 24px;
			height: 24px;
			@include margin-end(8px);
			svg {
				width: 24px;
				height: 24px;
				fill: currentColor;
			}
		}
		span>p {
			display: flex;
			align-items: center;
		}
		.or {
			display: none;
			@include margin-start(15px);
			font-size: var(--body-14);
			&:before {
				content: "";
				@include ab(-6px,n,n,6px);
				height: 10px;
				width: 1px;
				background: #fff;
			}
			&:after {
				content: "";
				@include ab(n,n,-7px,6px);
				height: 10px;
				width: 1px;
				background: #fff;
			}
		}
	}
	.btn-custom {
		&:hover {
			animation: shake 3s cubic-bezier(.36,.07,.19,.97) both;
			transform: translate3d(0, 0, 0);
			backface-visibility: hidden;
			perspective: 1000px;
		}
	}
	@media (min-width: 768px) {		
		.banner-content {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}
	@media (min-width: 992px) {
		.banner-content {
			padding: 30px 35px;
		}
		.right-content {
			display: flex;
			align-items: center;
			.or {
				display: inline-block;
			}
		}
	}
}
/*end single banner*/