.home-testimonials {
	.card_section-item {
		padding: 0;
	}
	.section_content-wrapper {
		position: relative;
	}
	.card_testimonial-author {
		text-transform: uppercase;
		font-weight: 500;
		margin: 0 0 var(--gutter-xxs);
		text-align: center;
		@media (min-width: 768px) {
			@include text-left;
		}
	}
	.card_testimonial-review {
		font-weight: 400;
		line-height: 1.618;
		p {
			margin: 0 0 var(--gutter-xs);
		}
	}
	.card_testimonial-image {
		img {
			@extend %radius-circle;
		}
	}
	.card_section-item-inner {
		padding: 30px;
	}
	.style-1 {			
		max-width: 900px;
		margin: 0 auto;
		padding: 35px 0;
		@media (min-width: 768px) {
			padding: 55px 0;
		}
		.card_section-item-inner {
			.card_testimonial-image {
				max-width: 110px;
				width: 100%;
				height: 110px;
				margin: 0 auto var(--gutter-xs);
			}
			.rating {
				text-align: center;
				margin-bottom: var(--gutter-xs);
			}
			.card_testimonial-review {
				font-size: 16px;
			}
			@media (min-width: 768px) {
				display: flex;
				.card_testimonial-content {
					@include padding-start(50px);
				}
				.rating {
					@include text-left;
				}
				.card_testimonial-review  {
					font-size: 18px;
				}
			}
		}
	}	
	.style-2 {
		.card_testimonial-image {
			display: flex;
			align-items: center;
			margin-bottom: var(--gutter-xs);
			.image {
				width: 60px;
				height: 60px;
			}
			.content {
				@include padding-start(var(--gutter-xs));
			}
		}
		.card_testimonial-review {
			font-size: var(--body-font-size);
		}
	}
}