.single-banner-filter {
	.single-banner {
		position: relative;
		z-index: 5;
	}
	.filter {
		@include ab(50%,0,n,0);
		transform: translateY(-50%);
	}
	.textbox {
		text-align: center;
	}
	.section-content {
		z-index: 11;
		position: relative;
	}
	.text-3 {
		li {
			line-height: 31px;
		}
		a {
			color: inherit;
		}
	}
}