.home-blog {
	position: relative;
	.section_content-wrapper {
		position: relative;
		.section_content-head {
			margin-bottom: 40px;
		}
		&.has-border-bottom {
			.section_title-wrapper {
				border-bottom: 1px solid var(--body-border-color);
			}
		}
		.section_content-body {
			.tns-carousel .blog-section {
				@include padding-start(0);
			}
		}
	}
	&.style-1 {
		.js-slider-controls a {
			margin-top: 30px;
		}
		.section_content-wrapper {
			padding: 20px; 
			@media (min-width: 1200px) {
				padding: 20px 25px;
			}
		}
		.featured_blog__item {
			.featured_blog__item-image {
				overflow: hidden;
				height: 100%;
				>a, span {
					height: 100%;
				}
				img {
					object-fit: cover;
				}
			}
		}
		@media (min-width: 992px) {
			.featured_blog__item {
				display: flex;
				header {
					width: 40%;
				}
				footer {
					width: 60%;
					padding: 15px 20px;
					.featured_blog__item-info {
						padding: 0;
					}
				}
			}
		}
		@media (min-width: 1025px) {
			.featured_blog__item {
				.featured_blog__item-image {
					img {						
						transform: scale(1,1);
						transition: 0.35s all ease-in-out;
					}
				}
				&:hover {
					.featured_blog__item-image {
						img {
							transform: scale(1.1,1.1);
						}
					}
				}
			}
		}
	}
	&.style-2 {
		.featured_blog__item {
			background: #fff;
			padding: 20px;
			@media (min-width: 1025px) {
				.featured_blog__item-image {
					img {
						transition: 0.3s all ease-in-out;
					}
				}
				&:hover {
					.featured_blog__item-image {
						img {
							filter: brightness(0.7);
						}
					}
				}
			}
		}
	}
}
.featured_blog__item {
	margin-bottom: 30px;
	.featured_blog__item-info {
		padding: 25px 0 0;
		text-transform: uppercase;
		font-size: calc(var(--body-font-size) - 1px);
		span {
			display: inline-block;
			margin-bottom: 18px; 
		}
		.featured_blog__item-publish {
			color: var(--main-color);
			@include margin-end(10px);
		}
		.featured_blog__item-author {
			color: var(--body-color);
			@include margin-end(10px);
		}
	}
	.featured_blog__item-title {
		margin-bottom: 20px;
		a {
			line-height: 1.25;
		}
		.featured_blog__item-publish {
			display: block;
			margin-top: 20px;
			font-size: var(--body-font-size);
		}
	}
	.featured_blog__item-excerpt {
		margin-bottom: 22px;
		@include clamp(2);
	}
}
.home-blog-v2 {
	.section-content {
		position: relative;
	}
	.article_published-at {
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 0.3px;
		font-size: calc(var(--body-font-size) - 2px);
	}
	.article_title {
		h3 {
			font-weight: 700;
			line-height: 1.3;
			font-size: 20px;
			margin-top: var(--gutter-xxs);
		}
	}
	.article_short-desc {
		margin-top: var(--gutter-xxs);
		line-height: 1.7;
		@include clamp(3);
	}
	.first-article {
		position: relative;
		.content {
			padding: 15px 20px;
			bottom: 0;
			top: auto;
			color: var(--text-miscellaneous);
			a {
				color: inherit;
			}
		}
		.image {
			img {
				filter: grayscale(0);
				transition: filter .4s cubic-bezier(.455,.03,.515,.955);
			}
			&:hover {
				img {
					filter: grayscale(1);
				}
			}
		}
		.article_published-at {
			display: none;
		}
		.article_short-desc {
			@include clamp(2);
		}
	}
	.style-1 {		
		.article {
			position: relative;
			.image {
				width: 100%;
			}
			.content {
				margin: 30px 0 27px;
				.article_published-at {
					span {
						color: var(--main-color);
					}
					.author {
						color: inherit;
						@include margin-start(5px);
					}
				}
			}
		}
	}
	.style-2 {
		.content {
			padding: 15px 20px;
			display: flex;
			bottom: 0;
			flex-direction: column;
			justify-content: center;
		}
		.article_title {
			@include clamp(2);
		}
		.article {
			position: relative;
			color: var(--text-miscellaneous);
			span, a {
				color: inherit;
			}
		}
		.content {
			padding-top: 0;
			.article_published-at {
				padding-top: 30px;
			}
		}
		.article_title h3 {
			font-size: 18px;
			margin-top: 6px;
			@media (min-width: 992px) {
				font-size: 16px;
			}
			@media (min-width: 1200px) {
				font-size: 18px;
			}
		}
	}
	@media (min-width: 992px) {
		.style-1 {
			.article {
				display: flex;
				align-items: center;
				.image {
					flex: 0 0 220px;
					max-width: 220px;
				}
				.content {
					margin: 0;
					@include margin-start(27px);
				}
			}
		}
		.first-article {
			.content {
				padding: 15px 30px 25px;
			}
			.article_published-at {
				display: block;
			}
			.article_title {
				h3 {
					font-size: var(--heading-size-h3);
				}
			}
		}
	}
}