.home-icon-box {
    .card_section-item-inner {
        display: flex;
        align-items: center;      
        height: 100%;
        .card_section-header {            
            .image__style {
                min-width: 70px;
                width: 100%;
                height: 70px;
            }
        }
    }
    .card_section-info {
        p {
         margin: 0;    
         font-size: var(--body-14); 
         line-height: 1.68;                        
     }
     .card_section-title {
        font-weight: 600; 
        font-size: var(--body-14);
        margin: 0;
        text-transform: uppercase;
        line-height: 1.5em;
    }
}
.card_section-item {
    padding: 20px 0;
    .card_section-figure {
        @include margin-end(15px);
        @extend %transition; 
        svg {
            width: 52px;
            height: 52px;
            path, rect {
                fill: var(--main-color);
            }
        }
        &:hover {
            @media (hover:hover) {
                transform: scale(1.1);
            }
        }
    }
    &.vertical-top {
        padding: 15px 20px;
        .card_section-figure {
            margin-bottom: 15px;
        }
        .card_section-info {
            .card_section-title {
                font-size: 18px;
                text-transform: unset;
                margin-bottom: 10px;
            }
            .card_section-subtitle {
                font-size: var(--body-font-size);
            }
        }
    }
}
    &.has-border {
        .section_content-body {
            @media (min-width: 768px) {
                .d-grid {
                    border-top: 1px solid var(--body-border-color);
                    border-bottom: 1px solid var(--body-border-color); 
                    .card_section-item {
                       border: 0;
                   }
               }
           }
       }
           .card_section-item {
            border-top: 1px solid var(--body-border-color);
            &:last-child {
                border-bottom: 1px solid var(--body-border-color);
            }
        }
    } 
}