.home-products-tab {
	position: relative;
	&.has-controls .featured_group_tab-header .section_title-wrapper {
		@media (min-width: 768px) {			
			@include padding-end(calc(100% - 90%));
		}
	}
	&.layout-boxed {
		>.container {
			padding: 0;
		}
		.section_content-wrapper {
			padding: 0 15px;
		}
	}
	.featured_group_tab-banner {
		.banner {
			position: relative;
			overflow: hidden;
			.banner-image {
				opacity: 1;
				transition: opacity .35s,transform .35s;
				transform: scale(1.1);
				backface-visibility: hidden;
			}
			.banner-textbox {
				padding: 20px 10px;
				display: flex;
				flex-wrap: wrap;
				height: 100%;
				align-content: space-between;
				@media (min-width: 1440px) {
					padding: 30px;
				}
				.heading {
					margin-bottom: 20px;
				}
				.subheading {
					line-height: 1.6;
				}
			}
			&:hover {
				@media (hover:hover) {
					.banner-image {
						opacity: 1;
						transform: scale(1);
					}
				}
			}
		}
	}
	.featured_group_tab-ui {
		position: relative;
		.featured_group_tab-header {
			flex-wrap: wrap;
			display: block;
			.section_title-wrapper {
				>div {
					margin: 0;
					line-height: 60px;
					p {
						margin: 0;
					}
				}
			}
		}
		.featured_group_tab-controls {
			display: block;
			overflow-x: auto;
			white-space: nowrap;
			a {
				color: var(--body-second-color);
				text-transform: uppercase;
				margin: 0 20px;
				line-height: 24px;
				display: inline-block;
				font-weight: 500;
				border-bottom: 2px solid transparent;
				&:first-child {
					@include margin-start(0);
				}
				&.active, &:hover {
					border-color: var(--main-color);
					color: var(--body-color);
				}
			}
		}
		@media (min-width: 768px) {			
			.featured_group_tab-header .section_title-wrapper {
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				justify-content: space-between;
				border-bottom: 1px solid var(--body-border-color);
			}
			.featured_group_tab-controls {
				a {
					margin: 0 25px;
				}
			}
		}
	}
	.row {
		.js-slider-controls .js-next-button {
			right: 15px;
		}
		.js-slider-controls .js-prev-button {
			right: 55px;
		}
	}
	@media (min-width: 992px) {
		&.layout-boxed {
			>.container {
				padding: 0 15px;
			}
			.section_content-wrapper {
				padding: 0;
			}
		}
	}
}
.featured_group_tab-ui {
	.featured_group_tab-content {
		>.featured_group_tab-pane {
			&:not(.active) {				
				@include ab(0,0,n,n);
				width: 100%;
				height: 0;
				visibility: hidden;
				opacity: 0;
			}
		}
	}
}