.logo-list_wrapper {
	.section_content-body {
		transition: 0.3s all ease-in-out;
		&:hover {
			@media (hover:hover) {				
				.logo-item {
					filter: blur(2px);
				}
			}
		}
	}
}
.logo-list {
	position: relative;
	.logo-list_wrapper {
		position: relative;
		padding: 50px 0;
		.logo-item {
			&:hover {
				@media (hover:hover) {
					filter: blur(0);
				}
			}
		}
	}
	.logo-item {
		transition: 0.3s all ease-in-out;
		svg {
			max-width: 120px;
			max-height: 90px; 
		}
		>a {
			display: inline;
		}
	}
	.section_content-body {
		position: relative;
	}
	&.style-2 {
		.row {
			align-items: center;
		}
		.section_title-wrapper {
			@media (min-width: 768px) {				
				margin: 0;
			}
			.section_title-text-1 {
				line-height: 1.5;
			}
		}
		.slider-controls {
			>a {
				background: none;
				box-shadow: none;
				color: var(--miscellaneous-color);
				path {
					fill: var(--miscellaneous-color);
					stroke: var(--miscellaneous-color);
				}
			}
			.slider-next-button {
				right: -30px;
			}
			.slider-prev-button {
				left: -30px;
			}
		}
	}
}

/*logo with banner*/
.logo-list-banner {
	.section_content-body {
		.logo-item {
			transition: 0.3s all ease-in-out;
			border-radius: 5px;
			.svg {
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
			}
			svg {
				max-width: 120px;
				max-height: 90px; 
			}
			img {
				border-radius: 5px;
			}
			&:hover {
				box-shadow: 0px 0px 10px -2px rgba(0,0,0,.3);
			}
			.see-all {
				flex-direction: column;
				svg {
					margin: 20px 0;
				}
				span {
					margin: 20px 0;
				}
			}
		}
	}
	.banner {
		position: relative;
		.image {
			height: 100%;
			span {
				height: 100%;
			}
			img {
				object-fit: cover;
			}
		}
		.textbox {
			padding: 40px 30px;
			bottom: 0;
			.text-1 {
				>* {
					font-size: 22px;
					font-weight: 600;
					line-height: 1.3;
				}
			}
			.text-2 {
				margin: 15px 0 20px;
				line-height: 1.5;
			}
			.button {
				@include ab(n,0,20px,0);
				text-align: center;
				&:hover {
					animation: shake 3s cubic-bezier(.36,.07,.19,.97) both;
					transform: translate3d(0, 0, 0);
					backface-visibility: hidden;
					perspective: 1000px;
				}
			}
		}
	}
}
/*end logo with banner*/